@import '../../appScss/reset';
@import '../../appScss/mixins.scss';
@import '../../appScss/arrow';

@font-face {
  font-family: 'Mont';
  src: url('../../fonts/Mont-Bold.otf') format('opentype');
  font-weight: 800;
}

@font-face {
  font-family: 'Mont';
  src: url('../../fonts/Mont-SemiBold.otf') format('opentype');
  font-weight: 700;
}

@font-face {
  font-family: 'Mont';
  src: url('../../fonts/Mont-Regular.otf') format('opentype');
  font-weight: 500;
}

$light-theme--primary: rgba(49, 50, 55, 1);
$light-theme--secondary: rgba(137, 147, 154, 1);
$light-theme--icons: rgba(180, 189, 195, 1);
$light-theme--elements: rgba(226, 230, 233, 1);
$light-theme--white: rgba(255, 255, 255, 1);
$light-theme--green: rgba(39, 174, 96, 1);
$light-theme--red: rgba(235, 87, 87, 1);

$font-main: 'Mont', sans-serif;
$font-bold: 800;
$font-semi-bold: 700;
$font-regular: 500;

.empty_cart_page {
  // display: flex;
  // flex-direction: column;
  margin: 40px auto 80px;

  font-family: $font-main;
  font-weight: $font-bold;

  @include OnTablet {
    margin-bottom: 64px;
  }

  @include OnPhone {
    margin-block: 24px 56px;
  }
}

.page {
  width: max-content;
  margin: 40px auto 80px;

  font-family: $font-main;
  font-weight: $font-bold;

  @include OnTablet {
    margin-bottom: 64px;
  }

  @include OnPhone {
    margin-block: 24px 56px;
  }
}

.empty_cart_info {
  margin-left: 40px;
  margin-bottom: 32px;
}

.cart_info {
  margin-bottom: 32px;
}

.link {
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.icon {
  width: 16px;
  height: 16px;
  color: $light-theme--primary;
}

.text {
  margin-left: 4px;
  font-weight: $font-semi-bold;
  font-size: 12px;
  color: $light-theme--secondary;
  line-height: 15px;

  &:hover {
    color: $light-theme--primary;
    transition: color 0.3s;
  }
}

.title {
  font-size: 48px;
  font-weight: $font-bold;
  line-height: 56px;

  color: $light-theme--primary;
}

.empty_cart_message {
  font-size: 20px;
  font-weight: $font-semi-bold;
  line-height: 26px;
  text-align: center;
}

.cart_content {
  display: flex;
  gap: 16px;
  justify-content: center;

  @include OnTablet {
    flex-direction: column;
    gap: 32px;
  }
}

.total_price {
  box-sizing: border-box;
  max-height: 206px;
  display: flex;
  flex-direction: column;
  padding: 24px;
  align-items: center;
  border: 1px solid $light-theme--elements;
}

.price {
  font-size: 32px;
  font-weight: $font-bold;
  line-height: 41px;
  color: $light-theme--primary;
}

.price_text {
  margin-bottom: 48px;
  font-weight: $font-regular;
  font-size: 14px;
  line-height: 21px;
  color: $light-theme--secondary;
}

.checkout_button {
  box-sizing: border-box;
  position: relative;
  width: 320px;

  font-size: 14px;
  font-weight: $font-semi-bold;
  text-decoration: none;
  text-align: center;
  line-height: 48px;

  border: 1px solid transparent;
  color: $light-theme--white;
  background-color: $light-theme--primary;

  @include OnTablet {
    width: 544px;
  }

  @include OnPhone {
    width: 240px;
  }

  &:hover {
    box-shadow: 0 3px 13px 0 rgba(23, 32, 49, 0.4);
    transition: box-shadow 0.3s;
  }

  &:active {
    color: $light-theme--green;
    border-color: $light-theme--elements;
    background-color: transparent;
    box-shadow: none;
  }

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: -50%;
    width: 100%;
    height: 1px;
    background-color: $light-theme--elements;
  }
}
